:root {
  --background: #ffffff;
  --foreground: #171717;
  --primary: #f2f2f2;
  --primary-600: #f2f2f2;
  --primary-700: #f2f2f2;
  --accent: #f2f2f2;
  --muted: #faf5ef;
  --border: #f2f2f2;
  --ring: var(--primary);
  @supports (color: color-mix(in lab, red, red)) {
    --ring: color-mix(in oklab, var(--primary) 50%, white);
  }
  --radius: 14px;
  --shadow-soft: 0 10px 30px rgba(0,0,0,0.08);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
    --primary: #ff8a3d;
    --primary-600: #ff7a1a;
    --primary-700: #e56f10;
    --accent: #4a2d17;
    --muted: #1a120b;
    --border: #3a2414;
    --ring: var(--primary);
    @supports (color: color-mix(in lab, red, red)) {
      --ring: color-mix(in oklab, var(--primary) 50%, black);
    }
  }
}
body {
  background: radial-gradient(1200px 600px at 80% -100px, var(--accent), transparent 70%),
    radial-gradient(800px 400px at -10% 10%, var(--muted), transparent 60%),
    var(--background);
  color: var(--foreground);
  font-family: var(--font-geist-sans), ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.btn-primary {
  background: linear-gradient(180deg, var(--primary) 0%, var(--primary-600) 100%);
  @supports (color: color-mix(in lab, red, red)) {
    background: linear-gradient(180deg, color-mix(in oklab, var(--primary) 92%, white) 0%, var(--primary-600) 100%);
  }
  color: white;
  border: 1px solid var(--primary-600);
  @supports (color: color-mix(in lab, red, red)) {
    border: 1px solid color-mix(in oklab, var(--primary-600) 70%, black 20%);
  }
  border-radius: calc(var(--radius) - 6px);
  box-shadow: 0 6px 16px var(--primary);
  @supports (color: color-mix(in lab, red, red)) {
    box-shadow: 0 6px 16px color-mix(in oklab, var(--primary) 24%, transparent);
  }
  transition: transform .12s ease, box-shadow .2s ease, filter .2s ease, background .2s ease;
}
.btn-primary:hover {
  filter: brightness(0.98);
  transform: translateY(-1px);
  box-shadow: 0 10px 22px var(--primary);
  @supports (color: color-mix(in lab, red, red)) {
    box-shadow: 0 10px 22px color-mix(in oklab, var(--primary) 32%, transparent);
  }
}
.focus-orange:focus {
  outline: none;
  box-shadow: 0 0 0 3px var(--ring);
  border-color: var(--primary-600);
}
.card {
  border: 1px solid var(--border);
  background: var(--accent);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--accent) 10%, var(--background));
  }
  backdrop-filter: blur(10px);
  border-radius: var(--radius);
  box-shadow: var(--shadow-soft);
}
.btn-secondary {
  background: transparent;
  color: var(--foreground);
  border: 1px solid var(--border);
  border-radius: calc(var(--radius) - 8px);
  transition: background .2s ease, border-color .2s ease, transform .12s ease;
}
.btn-secondary:hover {
  background: var(--accent);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--accent) 16%, transparent);
  }
  transform: translateY(-1px);
}
.x-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #5a5d72 rgba(0,0,0,0.08);
}
.x-scrollbar::-webkit-scrollbar {
  height: 12px;
}
.x-scrollbar::-webkit-scrollbar-track {
  background: rgba(0,0,0,0.06);
  border-radius: 999px;
}
.x-scrollbar::-webkit-scrollbar-thumb {
  background: #5a5d72;
  border-radius: 999px;
  border: 2px solid rgba(255,255,255,0.6);
}
.x-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #4a4d62;
}
.glass {
  background: var(--accent);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--accent) 10%, var(--background));
  }
  border: 1px solid var(--border);
  backdrop-filter: blur(12px);
  border-radius: var(--radius);
}
a.link {
  color: var(--primary);
  text-underline-offset: 3px;
}
a.link:hover {
  color: var(--primary-700);
